import React, { useEffect, useState } from "react";
import icons from "../icons/icons";
import StarBtn from "../components/starBtn";
import Navigation from "../components/navigetion";
import "../App.css";

const Home = () => {
  const [userInfo, setUserInfo] = useState({});

  useEffect(() => {
    const telegram = window.Telegram.WebApp;
    telegram.init();

    const user = telegram.initDataUnsafe?.user;
    if (user) {
      setUserInfo(user);
    }
  }, []);

  return (
    <div className="home-in">
      <div className="user-info">
        {userInfo.photo_url ? (
          <img src={userInfo.photo_url} alt="User profile" />
        ) : (
          <img src={icons.star} alt="Default icon" />
        )}
        <h3>@{userInfo.username || "username"}</h3>
      </div>
      <StarBtn className="home-star-btn" />
      <hr />
      <img className="home-img" src={icons.loadingImg} alt="" />
      <button className="green-btn">Claim</button>
      <Navigation />
    </div>
  );
};

export default Home;
